body {
   height: 100vh;
   overflow: hidden;
}

* {
   font-family: 'SF Pro Display Semibold', 'SF Pro Display';
}

h1 {
   font-family: 'SF Pro Display Semibold', 'SF Pro Display';
   font-weight: 600;
   font-style: normal;
   font-size: 20px;
}

h2 {
   font-family: 'SF Pro Display';
   font-weight: 400;
   font-style: normal;
   font-size: 16px;
}

p {
   font-family: 'SF Pro Display';
   font-weight: 400;
   font-style: normal;
   font-size: 14px;
   color: rgba(0, 0, 0, 0.65);
}

.preloader {
   display: flex;
   height: 100vh;
   justify-content: center;
   align-items: center;
}

.ant-btn-secondary {
   height: 60px;
   width: 250px;
   margin: 5px 0px;
   color: #1890ff;
   border: 1px solid #1890ff;
   line-height: 60px !important;
   &:hover {
      background: #1890ff;
      color: #fff;
   }
}

.auth__type {
   width: 430px;
   border-radius: 10px;
   box-shadow: 0px 0px 30px rgba(24, 144, 255, 0.28);
   padding: 30px 90px 90px 90px;

   .auth__type_server {
      position: absolute;
      width: 100%;
      left: 0;
      border-top: 1px solid #eee;
      margin-top: 15px;
      padding: 20px 90px 0px 90px;
      color: #b5b5b5;

      span {
         padding-right: 10px;
      }

      .ant-dropdown-link {
         color: #1890ff;
      }
   }

   h1, h2 {
      text-align: center;
   }
}

.page {
   height: 100vh;
   display: flex;
   flex-direction: column;
}

.page-top {
   display: flex;
}

.page__content {
   display: flex;
   height: 100%;
}

.dialog-container {
   width: 100%;

   .dialog {
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      width: 100%;
      height: calc(100vh - 70px);
      margin-left: -1px;
      background: #f9f9f9;
      border-left: 1px solid #e4e4e4;
      border-bottom: 1px solid #e4e4e4;
   
      .dialog__messages-viewport {
         height: 100%;
         overflow-y: auto;
         overflow-x: hidden;
   
         .dialog__messages {
            display: flex;
            flex-direction: column;
            justify-content: flex-end;
            padding: 20px;
            min-height: 100%;
         }
   
         .message {
            display: flex;
            max-width: 80%;
            margin: 10px 0px;
   
            .message__ava {
               margin-right: 10px;
            }
   
            .message__img {
               max-width: 150px;
               overflow: hidden;
               cursor: pointer;
   
               img {
                  width: 100%;
               }
            }
   
            .message-menu {
               ul {
                  margin-bottom: 0;
                  
                  li {
                     cursor: pointer;
   
                     &:hover {
                        text-decoration: underline;
                     }
                  }
               }
            }

            .message__read {
               width: 30px;
               height: 30px;
               fill: #1890ff;
               margin-left: 5px;
            }
         }
   
         .message-in {
            .message__content {
               font-size: 16px;
               background: #e4e4e4;
               padding: 10px;
               border-radius: 10px 10px 10px 0px;
   
               .message__name {
                  font-weight: 700;
                  font-size: 14px;
               }
            }
   
            .message__img {
               border-radius: 10px 10px 10px 0px;
            }
   
            .message__time {
               color: #717171;
               font-size: 14px;
               margin: 0px 0px 10px 10px;
            }
         }
   
         .message-out {
            display: flex;
            flex-direction: row-reverse;
            align-self: flex-end;
            max-width: 80%;
   
            .message__content {
               font-size: 16px;
               background: #1890ff;
               padding: 10px;
               color: #fff;
               border-radius: 10px 10px 0px 10px;
            }
   
            .message__img {
               border-radius: 10px 10px 0px 10px;
            }
   
            .message__time {
               color: #717171;
               font-size: 14px;
               margin: 10px 10px 10px 0px;
            }
         }
      }
   
      .new-message {
         display: flex;
         align-items: center;
         border-top: 1px solid #eee;
         padding: 10px 20px;
   
         .new-message__attach {
            width: 15px;
            height: 30px;
            fill: #1890ff;
            cursor: pointer;
            transition: .5s;
            &:hover {
               opacity: .8;
            }
         }
   
         .new-message__inp {
            width: 100%;
            border: 1px solid #eee;
            border-radius: 100px;
            padding: 10px;
            outline: none;
            margin: 0px 20px;
         }
   
         .new-message__send {
            width: 30px;
            height: 30px;
            fill: #1890ff;
            cursor: pointer;
            transition: .5s;
            &:hover {
               opacity: .8;
            }
         }
      }
   
      .reply-message {
         margin-left: 60px;
      }
   }

   .head-dialog {
      display: flex;
      flex-grow: 1;
      align-items: center;
      height: 70px;
      border-bottom: 1px solid #e4e4e4;
   
      .head-dialog__back {
         padding: 0px 25px;
         cursor: pointer;
      }
   
      .head-dialog__user {
         display: flex;
         align-items: center;
         max-width: 350px;
         padding: 10px;
         cursor: pointer;
   
         .head-dialog__ava {
            width: 50px;
            height: 50px;
            border-radius: 100%;
            overflow: hidden;
            margin-right: 15px;
            flex-shrink: 0;
            img {
               height: 100%;
            }
         }
   
         .head-dialog__photo-default {
            width: 100%;
            height: 100%;
            text-align: center;
            line-height: 50px;
            font-weight: 400;
            font-size: 23px;
            color: #1890FF;
            background: #e4e4e4;
         }
   
         .head-dialog__name {
            font-weight: 700;
            font-style: normal;
            font-size: 16px;
         }
   
         .head-dialog__status {
            font-weight: 400;
            font-size: 14px;
            color: rgba(0, 0, 0, 0.65);
         }
      }
   
      .head-dialog__lock {
         margin-left: auto;
         margin-right: 20px;
         svg {
            fill: #1890ff;
            width: 30px;
            height: 30px;
         }
      }
   
      .head-dialog__logout {
         margin-left: auto;
         margin-right: 20px;
         color: #1890ff;
         cursor: pointer;
      }
   
      .head-dialog__show_sidebar {
         display: none;
         cursor: pointer;
      }
   }
}

.sidebar {
   flex-shrink: 0;

   a {
      color: inherit;
   }

   .sidebar-viewport {
      height: 100vh;
      overflow-y: auto;

      .sidebar__item {
         position: relative;
         display: flex;
         max-width: 350px;
         padding: 15px 10px;
         border-bottom: 1px solid #e4e4e4;
         border-right: 1px solid #e4e4e4;
         cursor: pointer;
         transition: .5s;
         &:hover {
            background: #f9f9f9;
         }
   
         .sidebar__ava {
            width: 64px;
            height: 64px;
            margin-right: 15px;
            flex-shrink: 0;
   
            .sidebar__photo {
               height: 100%;
               border-radius: 100%;
               overflow: hidden;
   
               img {
                  height: 100%;
               }
   
               .sidebar__photo-default {
                  width: 100%;
                  height: 100%;
                  text-align: center;
                  line-height: 60px;
                  font-weight: 400;
                  font-size: 28px;
                  color: #1890FF;
                  background: #e4e4e4;
               }
            }
   
            .sidebar__badge {
               position: absolute;
               width: 20px;
               height: 20px;
               background: #1890ff;
               border-radius: 100%;
               color: #fff;
               text-align: center;
               font-weight: 400;
               font-size: 12px
            }
         }
   
         .sidebar__name {
            display: flex;
            font-weight: 700;
            font-style: normal;
            font-size: 16px;
   
            span {
               max-width: 240px;
               white-space: nowrap;
               overflow: hidden;
               text-overflow: ellipsis;
            }
   
            // &:after {
            //    content: '';
            //    display: block;
            //    width: 8px;
            //    height: 8px;
            //    background: #1890ff;
            //    border-radius: 100%;
            //    margin-top: 8px;
            //    margin-left: 5px;
            // }
         }
   
         .sidebar__message {
            font-weight: 400;
            font-size: 14px;
            line-height: 16px;
            color: rgba(0, 0, 0, 0.65);
         }
      }

      .sidebar__title {
         font-size: 16px;
         font-weight: 700;
         padding: 20px 0 0 10px;
         border-bottom: 1px solid #e4e4e4;
      }
   }

   .head-sidebar {
      display: flex;
      align-items: center;
      height: 70px;
      border-bottom: 1px solid #e4e4e4;
      border-right: 1px solid #e4e4e4;
   
      .head-sidebar__logo {
         width: 349px;
         padding-left: 25px;
   
         img {
            width: 139px;
            height: 49px;
         }
      }
   }

   .search {
      .ant-input {
         border: none;
         border-bottom: 1px solid #e4e4e4;
         border-radius: 0;

         &:focus {
            box-shadow: none;
         }
      }

      .ant-input-search-button {
         border-radius: 0;
      }
   }
}

.popup__img {
   width: 100%;
}

.ant-modal-close-x {
   width: 26px;
   height: 26px;
   line-height: 26px;
}

.profile {
   .profile__name {
      text-align: center;
      font-size: 28px;
      font-weight: 700;
      margin-top: 20px;
      padding-bottom: 15px;
   }

   .profile__info {
      display: flex;
      font-size: 14px;
      color: #000000a5;
      padding: 5px 0px;

      .profile__info_name {
         width: 100px;
      }

      .profile__info_users {
         display: flex;
         flex-wrap: wrap;

         .profile__info_user {
            width: 130px;
            text-align: center;
            padding: 0px 10px 20px 10px;
            cursor: pointer;

            .profile__info_ava {
               width: 50px;
               height: 50px;
               border-radius: 100px;
               text-align: center;
               line-height: 50px;
               font-weight: 400;
               font-size: 23px;
               color: #1890FF;
               background: #e4e4e4;
               margin: 0 auto;
               margin-top: 10px;
            }
         }
      }
   }

   .profile__open_chat {
      margin-top: 30px;
      cursor: pointer;
      color: #1890ff;
      &:hover {
         text-decoration: underline;
      }
   }
}


@media screen and (max-width: 675px) {
   .sidebar {
      width: 100%;

      .sidebar__item {
         max-width: 100%;
         width: 100%;
      }
   }
}